

#wrapper {
  height: auto;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

#wrapper:after {
  content: "";
  display: table;
  clear: both;
}

#slider {
  width: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);

}

header {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(transparentize($black, .3), transparentize($black, .3) 100%),
              // linear-gradient(0deg, #fff, transparent),
              $black url('../data/images/myself.jpg') no-repeat center;

  background-size: cover;
  margin: 0;
}

section:after {
    content: "";
    display: table;
    clear: both;
}

