nav {
  @extend %transition-all;
  position: fixed;
  top: 0;
  left: 0;
  height: 50px;
  width: 100%;
  text-align: center;
  z-index: 4;
  @media #{$brk-wide} {

  }
}

.logo {
  @extend %transition-all;
  font-family: 'Play', Arial, sans-serif;
  font-size: 1.125em;
  width: 120px;
	margin: 0;
  position: fixed;
  top: 1em;
  right: 1em;
	display: inline-block;
  color: $white !important;
  text-align: center;
  z-index: 5;
  padding: 0 5px 2px;
  border-radius: 15px;
  border: 3px solid $white;
  .bold {
    font-weight: bold;
    color: $color-1;
  }
  &:hover {
    color: transparentize($color-1, .2) !important;
    box-shadow: 0 0 5px $white, inset 0 0 5px $white;
    border-color: transparentize($color-1, .2);
    .bold {
      color: $white;
    }
    &:before {
      border-color: $white;
    }
  }
  &:before {
    content: '';
    border: 3px solid $color-1;
    top: -3px;
    left: -3px;
    height: 29px;
    width: 29px;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    border-right: none;
    background: none;
    display: block;
    position: absolute;
    pointer-events: none;
  }
  &:after {
    content: '';
    border-top: 3px solid $black;
    border-bottom: 3px solid $black;
    top: -3px;
    left: 26px;
    height: 29px;
    width: 5px;
    border-right: none;
    background: none;
    display: block;
    position: absolute;
  }
  @media #{$brk-narrow} {
    right: 0;
    left: 50%;
    transform: translate(-50%);
  }
}



.nav-btn {
  width: 50px;
  height: 50px;
  position: fixed;
  padding: 10px;
  left: 1em;
  display: inline-block;
  text-align: left !important;
  z-index: 3;
  border-bottom: 2px solid $color-1;
  .closed {
    @extend %transition-all;
    cursor: pointer;
    .nav-dash {
      height: 2px;
      width: 24px;
      margin: 5px auto;
      background: $white;
      position: relative;

    }
    .dash1 {
      @extend %transition-all;
      transform: rotate(0deg);
    }
    .dash2 {
      @extend %transition-all;
      transform: rotate(0deg);
    }
    .dash3 {
      @extend %transition-all;
      transform: rotate(0deg);
      display: block;
    }
  }
  .opened {
    @extend %transition-all;
    margin-left: 10px;

    .nav-dash {

    }
    .dash1 {
      @extend %transition-all;
      transform: rotate(135deg);
      right: 5px;
      top: 6px;
    }
    .dash2 {
      @extend %transition-all;
      transform: rotate(-135deg);
      top: -1px;
      right: 5px;
    }
    .dash3 {
      @extend %transition-all;
      transform: rotate(135deg);
      display: none;
    }
  }
  @media #{$brk-narrow} {
    display: none;
  }
}

#nav-list {
  @include flexbox();
  @include transition(1s);
  position: absolute;
  opacity: 0;
  background: $color-1;
  z-index: 1;
  -webkit-flex-direction: column;
  flex-direction: column;
  top: 48px;
  left: 1em;
  width: 0;
  height: 0;
  overflow: hidden;
  clear: both;
  text-align: center;
  -webkit-align-items: stretch;
  align-items: stretch;
  -webkit-justify-content: center;
  justify-content: center;
  box-shadow: inset -6px 6px 20px transparentize($black, .8),  -1px 1px 4px transparentize($black, .8);
  border: 2px solid $color-1;
  .active {
  	box-shadow: inset 0 0 100px transparentize($white, .1);
    font-size: 1.2em;
    @media #{$brk-narrow} {
      box-shadow: none;
      font-size: 1em;
      border-bottom: 1px solid $color-1;
    }
  }
  .nav-item {
    @include flexbox();
    -webkit-align-items: center;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 100%;
    &:nth-child(2) {
      margin-right: 150px;
    }
    .nav-link {
      @extend %transition-all;
      background: transparent;
      flex-basis: 100%;
      text-align: center;
      text-transform: capitalize;
      text-decoration: none;
      text-shadow: 0 0 3px transparentize($black, .7);
      box-shadow: inset 0 0 30px transparentize($white, .7);
      color: $black;
      font-weight: lighter;
      font-size: 1em;
      @media #{$brk-narrow} {
        color: $white;
        box-shadow: none;
      }
    }

  }
  @media #{$brk-narrow} {
    opacity: 1;
    background: transparent;
    flex-direction: row;
    align-items: center;
    position: static;
    margin: 1em auto;
    width: 700px;
    height: 45px;
    border: none;
  }

}
