#home {
  @include transition(1s);
  width: 80%;
  max-width: 300px;
  text-align: center;
  margin: 0 auto;
  color: $white;
  h1 {
    margin: 0;
    text-transform: uppercase;
    font-family: 'Play', Arial, sans-serif;
    text-shadow: -3px 3px 1px transparentize($black, .9);
    font-weight: normal;
    font-size: 3em;
    &:after {
      content: '';
      margin: 0 auto;
      display: block;
      height: 1px;
      width: 150px;
      background: $color-1;
      @media #{$brk-narrow} {
        height: 2px;
        width: 300px;
      }
    }
    @media #{$brk-narrow} {
      font-size: 4em;

    }
  }
  h2 {
    text-transform: uppercase;
    font-size: Helvetica, Arial, sans-serif;
    font-weight: lighter;
    text-shadow: 1px 1px 3px transparentize($black, .9);
    @media #{$brk-narrow} {
      font-size: 2em;
    }
  }
  @media #{$brk-narrow} {
    max-width: 450px;
  }
}
