/* Navbar animations */
.toggle-menu-width-on {
  animation: toggle-menu-width-on .5s ease-out .0s 1 normal forwards running;
  opacity: 1 !important;
}

.toggle-menu-height-on {
  animation: toggle-menu-height-on .5s ease-out .0s 1 normal forwards running;
  opacity: 1 !important;
}

.toggle-menu-width-off {
  animation: toggle-menu-width-off .2s ease-out .0s 1 normal forwards running;
  opacity: 1 !important;
}

.toggle-menu-height-off {
  animation: toggle-menu-height-off .2s ease-out .0s 1 normal forwards running;
  opacity: 1 !important;
}

@keyframes toggle-menu-width-on {
  from {
    width: 0;

  }
  to {
    width: 50vw;
  }
}

@keyframes toggle-menu-height-on {
  from {
    height: 0;
    width: 50vw;
  }
  to {
    height: 300px;
    width: 50vw;

  }
}

@keyframes toggle-menu-width-off {
  from {
    width: 50vw;

  }
  to {
    width: 0 ;
  }
}

@keyframes toggle-menu-height-off {
  from {
    height: 300px;
    width: 50vw;

  }
  to {
    height: 0;
    width: 50vw;
  }
}

/* Animated class defines the divv to be following to have animation */
.animated {

}

.slide-in-left {
  animation: slide-in-left .5s ease-out .5s 1 normal forwards running;
}

.slide-out-left {
  animation: slide-out-left .5s ease-out .0s 1 normal forwards running;
}

/* Main div slide in and out animation */
@keyframes slide-out-left {
    from {
      left: 0;
    }
    to {
      left: -100%;
    }
}




@keyframes slide-in-left {
  from {
    left: -100%;
  }
  to {
    left: 0;
  }
}
